"use client";
import "../globals.css";
import Link from "next/link";
import { useDarkMode } from "../contexts";
import FancyDropdown from "../../components/ui/dropdown";

import {useEffect } from "react";
import { redirect, usePathname } from "next/navigation";

function SideBar() {
  
  const menuItems = [
    { name: "Boards", path: "/organiser/boards" },
    { name: "Tasks", path: "/organiser/tasks" },
  ];

  return (
      <ul>
        {menuItems.map((item) => (
          <li key={item.path} className="mb-2">
            <Link href={item.path}>{item.name}</Link>
          </li>
        ))}
      </ul>
  );
}

export default function OrganiserLayout({ children }) {

  const pathname = usePathname();
  const { darkMode } = useDarkMode();
  

  useEffect(() => {
    if (pathname === "/organiser") {
      redirect("/organiser/boards");
    }
  },
  [pathname]);

  return (
    <div>
      <aside
        className={`w-50 p-6 transition-colors fixed top-16 left-0 h-full ${
          darkMode ? "bg-gray-800" : "bg-white"
        } shadow-lg`}
      >
        <SideBar/>
        <FancyDropdown />

      </aside>
      <main className="flex flex-1 p-6 overflow-auto mt-16">
      {children}
      </main>
    </div>
  );
}
